<template>
  <div class="linear" ref='linear' style='width: 400px;height: 300px;'>

  </div>
</template>

<script>
  export default{
    mounted(){
      this.init();
    },
    methods: {
      init(){
        let myChart = this.$echarts.init(this.$refs.linear);
        var img = 'data:image/png;base64,';
        var trafficWay = [{
            name: '正高级教师',
            value: 20
        }, {
            name: '高级教师',
            value: 10
        }, {
            name: '一级教师',
            value: 30
        }, {
            name: '二级教师',
            value: 40
        }, {
            name: '三级教师',
            value: 50
        }];

        var data = [];
        var color = ['#0090FF', '#0090FF', '#0090FF', '#0090FF', '#0090FF', '#0090FF', '#0090FF']
        for (var i = 0; i < trafficWay.length; i++) {
            data.push({
                value: trafficWay[i].value,
                name: trafficWay[i].name,
                itemStyle: {
                    normal: {
                        borderWidth: 5,
                        shadowBlur: 20,
                        borderColor: color[i],
                        shadowColor: color[i]
                    }
                }
            }, {
                value: 2,
                name: '',
                itemStyle: {
                    normal: {
                        label: {
                            show: false
                        },
                        labelLine: {
                            show: false
                        },
                        color: 'rgba(0, 0, 0, 0)',
                        borderColor: 'rgba(0, 0, 0, 0)',
                        borderWidth: 0
                    }
                }
            });
        }
        var seriesOption = [{
            name: '',
            type: 'pie',
            clockWise: false,
            radius: [105, 109],
            hoverAnimation: false,
            itemStyle: {
                normal: {
                    label: {
                        show: true,
                        position: 'outside',
                        color: '#ddd',
                        width: 50,
                        formatter: function(params) {
                            var percent = 0;
                            var total = 0;
                            for (var i = 0; i < trafficWay.length; i++) {
                                total += trafficWay[i].value;
                            }
                            percent = ((params.value / total) * 100).toFixed(0);
                            if (params.name !== '') {
                                return   params.value + '个';
                            } else {
                                return '';
                            }
                        },
                    },
                }
            },
            data: data
        }];
        let option = {
            backgroundColor: 'transparent',
            color: color,
            title: {
                text: '',
                top: '48%',
                textAlign: "center",
                left: "49%",
                textStyle: {
                    color: '#fff',
                    fontSize: 22,
                    fontWeight: '400'
                }
            },
            graphic: {
                elements: [{
                    type: "image",
                    z: 3,
                    style: {
                        image: img,
                        width: 178,
                        height: 178
                    },
                    left: 'center',
                    top: 'center',
                    position: [100, 100]
                }]
            },
            tooltip: {
                show: false
            },
            toolbox: {
                show: false
            },
            series: seriesOption
        }
        myChart.setOption(option);

      }
    }
  }
</script>

